import React from 'react'
import Link from 'gatsby-link'
import Gallery from 'react-photo-gallery';
import Navigoat from '../layouts/navigoat.js'

export default class FoodGallery extends React.Component {
  render() {
    return (
      <div>
        <Navigoat active_tab='gallery'/>
        <Gallery photos={PHOTO_SET} onClick={this.openLightbox}/>
      </div>
    );
}
}

